@import url(https://fonts.googleapis.com/css2?family=Yeon+Sung&display=swap);
.home {
  width: 100%;
  height: 100vh;
  margin: 0;
  background: #121022;
}

.home__description {
  width: 85%;
  margin: 3% auto;
  text-align: center;
  font-family: 'Yeon Sung', cursive;
  font-size: 1.2rem;
  color: #ffffff;
}
.movie__description {
  position: absolute;
  max-width: 600px;
  bottom: 2rem;
  margin-left: 2rem;
  color: #ffffff;
}

.movie__description p {
  font-size: 1.2rem;
  margin-top: 15px;
}

@media screen and (max-width: 1090px) {
  .home__description {
    margin: 0%;
    width: 100%;
    background: #121022;
  }
}
.nav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  border-radius: 3px;
  width: 100%;
  padding: 10px 20px;
  transition: all 300ms ease-in-out;
  font-family: 'Yeon Sung', cursive;
  font-size: 1.4rem;
  z-index: 1;
}

.nav__logo {
 display: flex;
 justify-content: center;
 align-items: center;
}

.nav__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5%;
}

.nav__menu__item {
  padding: 8px 12px;
  margin: 0 4px;
  cursor: pointer;
  border-radius: 20px;
  border-color: #fff;
  color: #fff;
  font-size: 1.8rem;
  transition: background-color 0.5s;
}

.nav__menu__item:hover {
  border-color: black;
  background-color: gold;
  color: black;
  text-decoration: none;
}

.nav__logo {
  margin: 0 10%;
}

.nav__logo a {
  color: #fff;
  font-size: 2.3rem;
}

.nav__logo a:hover {
  color: #fff;
  text-decoration: none;
}

.nav__logo img {
  width: 50px;
  height: 50px;
  margin-right: 15px;
  margin-bottom: 5px;
}

.nav__menu__toggle {
  position: absolute;
  top: 24px;
  right: 32px;
  font-size: 24px;
  color: #fff;
  display: none;
}

.nav__toggle__btn {
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 1.8rem;
}

.nav__toggle__btn:focus {
  outline: none;
}

@media screen and (max-width: 970px ) {
  .nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .nav__logo {
    margin: 0;
  }

  .nav__menu {
    display: none;
  }

  .nav__menu__toggle {
    display: block;
    top: 16px;
    right: 16px;
  }

  .nav__toggle__btn {
    display: block;
    top: 16px;
    right: 16px;
  }

  .nav__toggle__btn:active {
    color: gold;
    text-decoration: none;
  }

  .nav__menu__open {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin-top: 50px;
    margin-left: 20px;
    font-size: 1rem;
    color: #121022;
  }

  .nav__menu__item:hover {
    background: #121022;
    color: gold;
    text-decoration: none;
  }

  .nav__logo a {
    font-size: 1.8rem;
  }

  .nav__menu__item {
    font-size: 1.5rem;
  }

  .nav__toggle__box {
    position: absolute;
    top: 3px;
    right: 16px;
  }
}

.tableBox {
  padding: 1.5rem;
  color: #4a485d;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  background: #0F0D1A;
}

.info__header {
  padding-bottom: 1rem;
  color: #FC0014;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}

table {
  font-size: 1.5rem;
  margin: 0 auto;
  text-align: center;
  width: 80%;
}

td {
  text-align: center;
  width: 20%;
  padding: 15px;
}

.info {
  font-size: 2rem;
  font-weight: 600;
}

.info__value {
  font-size: 1.6rem;
  font-weight: 700;
  color: #ffffff;
}

@media screen and (max-width: 1090px) {
  .info__header {
    text-align: center;
  }
  table {
    width: 100%;
  }
  tr {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0 30px;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  td {
    width: 100%;
    padding: 10px 0;
  }

  .info {
    font-size: 1.8rem;
  }

  .info__value {
    font-size: 1.4rem;
    text-align: start;
  }

}

.cast {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  margin: 10px;
}

.cast img {
  width: 200px;
  height: 300px;
  border-radius: 5px;
}

.cast p {    
  margin: 8px auto;
}

@media screen and (max-width: 1090px) {

  .cast img {
    width: 100%;
    height: 200px;
  }
}

.cast_box {
  text-align: center;
  background: #0F0D1A;
}

.cast_box h1 {
  font-size: 2.5rem;
  font-weight: 900;
  color: #ffffff;
}

.casts {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  text-align: center;
  width: 100%;
  padding: 2% 5%;
  font-size: 1.3rem;
  font-weight: 500;
  background: #0F0D1A;
  color: #ffffff;
}

.cast__toggle__btn {
  text-align: center;
  padding: 5% 0;
  background: #0F0D1A;
}
.cast__toggle__btn :hover {
 background: #ffffff;
 color: #16152d;
 cursor: pointer;
}

.cast__toggle__btn button {
  background: #16152d;
  color: #ffffff;
  border-radius: 15px;
  font-size: 1.4rem;
  font-weight: 600;
  border: 1px solid #ffffff;
  outline: none;
  padding: 15px;
}

@media screen and (max-width: 1090px) {

  .casts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
  }
  
}
.movie {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  border-radius: 10px;
  background: #16152d;
}

.movie__top1 {
  border: 10px solid rgb(248, 248, 103);
  margin: 0%;
}

.movie img {
  position: relative;
  width: 70%;
  height: 50%;
  top: -30px;
  left: 30px;
  border-radius: 5px;
}

.movie__info {
  margin-top: 10px;
  width: 100%;
  height: 100%;
  text-align: start;
  color: #ffffff;
}

.movie__info h1 {
  font-size: 1.8rem;
  width: 100%;
}


.movie__info h3 {
  margin: 20px auto;
  color: #4a485d;
}

.movie__info p {
  font-size: 1rem;
}

.movie__info span {
  color: #dcbe3e;
  font-size: 1.8rem;
  padding-right: 10px;
}

.ratingStars {
  color: #dcbe3e;
  margin-bottom: 5px;
}

@media screen and (max-width: 1090px) {

  .movie {
    width: 100%;
    padding-bottom: 5%;
    margin: 2% auto;
  }

  .movie__info h3 {
    font-size: 0.9rem;
  }

  .movie a {
    width: 90%;
  }

  .movie img {
    width: 70%;
    left: 20px;
  }

  .movie__info h1{
    font-size: 1.5rem;
  }
  
}

.container {
  margin: 0;
  padding: 80px 30px;
  width: 100vw;
  background: #121022;
}

.section__title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 900;
  margin: 30px;
  color: #fff;
  font-family: "Yeon Sung", cursive;
}

.movie__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: flex-start;
  grid-gap: 50px 30px;
  width: 80%;
  margin: 0 auto;
}

.moreBtn {
  text-align: center;
  margin-top: 50px;
}

.moreBtn button {
  border-radius: 10px;
  background: #1d0bd2;
  color: #ffffff;
  font-size: 1.5rem;
  padding: 10px;
  font-family: 'Yeon Sung', cursive;
  border: none;
  outline: none;
  cursor: pointer;
}

.moreBtn :hover {
  background: #ffffff;
  color: #1d0bd2;
}

@media screen and (max-width: 1090px) {
  .section__title {
    font-size: 2rem;
    margin-bottom: 15%;
  }
  .movie__container {
    grid-template-columns: 1fr;
    width: 100%;
    padding: 0;
  }
  
}
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

a {
  text-decoration: none;
  background-color: transparent;
}
